.navbar {
  height: 100px;
  min-width: 700px;
  background-color: #5981ea;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.homeIcon {
  size: 100em;
  padding-left: 40px;
  :hover {
    cursor: pointer;
  }
  padding-right: 20px;
}

.nav-items {
  padding-left: 20px;
  display: flex;
  justify-content: space-between;
  width: 500px;
  padding-right: 40px;
}
